import { useEffect } from 'react';
import AboutHotel from '../component/about-hotel/about-hotel';
import Accommodations from '../component/accommodations/accommodations';
import Hero from '../component/hero/hero';
import Other from '../component/other/other';

interface props {
  hotelName: string;
}

function HotelHome({ hotelName }: props) {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div>
      <Hero
        images={[
          {
            url: '/images/hotel/home-2.jpg',
          },
          {
            url: '/images/hotel/home-1.jpg',
          },
          {
            url: '/images/hotel/home-3.jpg',
          },
        ]}
      />
      <AboutHotel hotelName={hotelName} />
      <Accommodations />
      <Other />
    </div>
  );
}

export default HotelHome;
