import { Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import './accommodations.css';

function Accommodations() {
  return (
    <div className="accommodations-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center ">
          <Col sm={5} className="text-center p-5">
            <h3 className="mb-5">ALOJAMIENTOS</h3>
            <p className="mb-5">
              Las habitaciones y suites del Riu Cancún All Inclusive incluyen
              balcones o terrazas con vista al mar oa la calle, ventiladores de
              techo, así como aire acondicionado, minibar y dispensadores de
              licores.
            </p>

            <Link className="btn btn-info" to="/accommodations">
              leer más
            </Link>
          </Col>
          <Col sm={5}>
            <div className="m-4">
              <Link to="/accommodations">
                <img
                  className="side-img"
                  src="/images/hotel/accommodation/accommodations-1.jpg"
                  alt="imf"
                />
              </Link>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Accommodations;
