import { Col, Container, Row } from 'react-bootstrap';

import './about-hotel.css';

interface props {
  hotelName: string;
}

function AboutHotel({ hotelName }: props) {
  return (
    <div className="hotel-about-us-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h3>{hotelName}</h3>
            <h4 className="about-us-head">
              {hotelName} - Todo Incluido - Cancún, México
            </h4>
          </Col>
        </Row>
        <Row className="justify-content-md-center mt-5">
          <Col lg="9">
            <p>
              El Hotel Riu Cancún (Todo Incluido 24 horas) está situado en el
              corazón de una de las zonas más turísticas de Cancún, en una
              ubicación privilegiada sobre una playa de arena blanca y aguas
              turquesas.
            </p>
            <p>
              Si buscas unos días para mimar tu cuerpo, te recomendamos nuestro
              Renova Spa con una amplia gama de tratamientos, además de gimnasio
              y jacuzzi. O simplemente recuperar fuerzas en vacaciones tomando
              el sol en cualquiera de nuestras tres piscinas de agua dulce.
            </p>
            <p>
              La oferta culinaria del hotel incluye un restaurante buffet con
              una amplia variedad de platos, así como restaurantes temáticos que
              satisfarán tu paladar con la mejor cocina internacional.
            </p>
          </Col>
        </Row>
        <Row className="about-us-img justify-content-md-center mt-3 text-center">
          <Col xs={4} sm={3} lg="3">
            <img src="/images/hotel/gallery/gallery-3.jpg" alt="img" />
          </Col>
          <Col xs={4} sm={3} lg="3">
            <img src="/images/hotel/gallery/gallery-5.jpg" alt="img" />
          </Col>
          <Col xs={4} sm={3} lg="3">
            <img src="/images/hotel/gallery/gallery-1.jpg" alt="img" />
          </Col>
        </Row>
        <Row className="justify-content-md-center mt-4">
          <Col lg="9">
            <p>
              El sistema Todo Incluido 24h del Hotel Riu Cancún hará que nunca
              te aburras, ya que ofrece una larga lista de actividades para
              disfrutar cada día con tu familia, amigos o en pareja. Ofrecemos
              Gimnasio y animación para todas las edades, incluyendo el programa
              RiuLand para los más pequeños y opciones de animación diurna y
              nocturna para los adultos. Puedes ver espectáculos, escuchar
              música en vivo, disfrutar de deportes acuáticos y bailar con
              excelente música en la discoteca “Pacha” por la noche.
            </p>
            <p>
              Los alrededores de Cancún ofrecen impresionantes espacios
              naturales y playas paradisíacas. No te puedes perder los tours más
              populares de Cancún, “Chichén Itzá” (las fantásticas ruinas mayas)
              o Isla Mujeres donde podrás presenciar la protección y
              reproducción de las tortugas. También puede visitar los diversos
              museos que muestran la historia de la región o subir a la torre
              escénica para disfrutar de unas vistas inmejorables y coloridas de
              la ciudad. Y esto es solo el comienzo, ya que Cancún cuenta con un
              sinfín de actividades más para garantizarte unas vacaciones
              inolvidables.
            </p>
            <p>
              Todo esto se une para hacer de Cancún un destino inigualable. Para
              más información puedes consultar nuestra guía de viaje de Cancún.
              Disfruta de las mejores ofertas de México y pasa un rato de relax
              en el hotel.
            </p>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default AboutHotel;
