import SimpleImageSlider from 'react-simple-image-slider';
import MediaQuery from 'react-responsive';

interface props {
  images: any;
}

function Hero({ images }: props) {
  return (
    <div>
      <MediaQuery minWidth={1224}>
        <SimpleImageSlider
          width={'100%'}
          height={'70vh'}
          images={images}
          showBullets={true}
          showNavs={true}
          slideDuration={1}
          autoPlay={true}
        />
      </MediaQuery>
      <MediaQuery maxWidth={1224}>
        {' '}
        <SimpleImageSlider
          width={'100%'}
          height={'40vh'}
          images={images}
          showBullets={true}
          showNavs={true}
          slideDuration={1}
          autoPlay={true}
        />
      </MediaQuery>
    </div>
  );
}

export default Hero;
