import { Col, Container, Row } from 'react-bootstrap';

import './other.css';

const highlights = [
  'Todo Incluido 24 horas',
  'En primera línea de playa',
  'Otorgada certificación GOLD por el Sistema de Sostenibilidad de Travelife',
  'Un amplio programa de entretenimiento para adultos y niños ofrecido por el equipo de animación de RIU',
  'WiFi gratis en todo el hotel',
  'Restaurantes principales y temáticos',
  '3 piscinas y infinity pool',
  'Gimnasio y bañera de hidromasaje gratis',
];

const activities = [
  '2 canchas de tenis, cancha de voleibol de playa',
  'Gimnasio y baño de vapor',
  'Bañera de hidromasaje',
  'Actividades fitness en grupo en la zona RiuFit (diario)',
  'Equipos de catamarán, kayak, bodyboard y snorkel',
  '1 lección introductoria de buceo en piscina',
  'Programa de animación diurna para niños de 4 a 7 años y de 8 a 12 años en "RiuLand" (diario)',
  'Programa de entretenimiento diurno para adultos (diario)',
  'Programa Riu de noche, espectáculos o música en vivo (diariamente)',
];

const services = [
  'Restaurante "Albatros"',
  'Restaurante mexicano "María Margarita"',
  'Restaurante asiático "Shangri-la"',
  'Pizzería',
  'Restaurante/asador junto a la piscina "Gaviotas"',
  'Lobby bar "Frida" con terraza',
  'Barra "Kahlo"',
  'bar salón "Calypso"',
  'Bar deportivo',
  'Bar junto a la piscina con bar en la piscina',
  'Tres piscinas (960 / 260 / 252 m²), una con sección infantil (57 m²)',
  'Tumbonas, sombrillas y toallas gratuitas en zona de piscina',
  'Baño de hidromasaje gratis',
  'Terraza para tomar el sol',
  'Kids club y parque infantil "RiuLand"',
];

function Other() {
  return (
    <div className="other-div pt-5 pb-5">
      <Container>
        {highlights.length > 0 && (
          <>
            <Row className="justify-content-md-center text-center mb-5">
              <Col lg="8">
                <h3>Lo más destacado del Hotel Riu Cancún:</h3>
              </Col>
            </Row>
            <Row className="justify-content-md-center text-left mb-5">
              <Col lg="10">
                <ul className="other-list">
                  {highlights.map((value, i) => {
                    return (
                      <li key={i}>
                        <i className="fa fa-check-circle"></i> {value}
                      </li>
                    );
                  })}
                </ul>
              </Col>
            </Row>
          </>
        )}
        {activities.length > 0 && (
          <>
            <Row className="justify-content-md-center text-center mb-5">
              <Col lg="8">
                <h3>Actividades en Hotel Riu Cancún:</h3>
              </Col>
            </Row>
            <Row className="justify-content-md-center text-left mb-5">
              <Col lg="10">
                <ul className="other-list">
                  {activities.map((value, i) => {
                    return (
                      <li key={i}>
                        <i className="fa fa-check-circle"></i> {value}
                      </li>
                    );
                  })}
                </ul>
              </Col>
            </Row>
          </>
        )}
        {services.length > 0 && (
          <>
            <Row className="justify-content-md-center text-center mb-5">
              <Col lg="8">
                <h3>Servicios en Hotel Riu Cancún:</h3>
              </Col>
            </Row>
            <Row className="justify-content-md-center text-left mb-5">
              <Col lg="10">
                <ul className="other-list">
                  {services.map((value, i) => {
                    return (
                      <li key={i}>
                        <i className="fa fa-check-circle"></i> {value}
                      </li>
                    );
                  })}
                </ul>
              </Col>
            </Row>
          </>
        )}
      </Container>
    </div>
  );
}

export default Other;
